                </li>
            </ul>
            <div class="tab-content">
                <div id="tab-1" class="tab-pane fade p-0 active show">
                    <div class="row g-5">

                        <div class="col-lg-3 col-md-4 col-sm-6" v-for="rutina_lunes in lunes">
                            <div class="bg-dark rounded text-center py-5 px-3">
                                <h6 class="text-uppercase-white text-light mb-3">{{rutina_lunes.repeticiones}}</h6>
                                <h5 class="text-uppercase text-primary mb-4">{{rutina_lunes.ejercicio}}</h5>
