                <div id="tab-2" class="tab-pane fade p-0">
                    <div class="row g-5">
                        
                        <div class="col-lg-3 col-md-4 col-sm-6" v-for="rutina_martes in martes">
                            <div class="bg-dark rounded text-center py-5 px-3">
                                <h6 class="text-uppercase-white text-light mb-3">{{rutina_martes.repeticiones}}</h6>
                                <h5 class="text-uppercase text-primary mb-4">{{rutina_martes.ejercicio}}</h5>
                                <img class="img-fluid rounded" v-bind:src="rutina_martes.url_img" alt="imagen_ejercicio" />
                            </div>
