                        <div class="col-lg-3 col-md-4 col-sm-6" v-for="rutina_miercoles in miercoles">
                            <div class="bg-dark rounded text-center py-5 px-3">
                                <h6 class="text-uppercase-white text-light mb-3">{{rutina_miercoles.repeticiones}}</h6>
                                <h5 class="text-uppercase text-primary mb-4">{{rutina_miercoles.ejercicio}}</h5>
                                <img class="img-fluid rounded" v-bind:src="rutina_miercoles.url_img" alt="imagen_ejercicio" />
