                    <div class="row g-5">

                        <div class="col-lg-3 col-md-4 col-sm-6" v-for="rutina_jueves in jueves">
                            <div class="bg-dark rounded text-center py-5 px-3">
                                <h6 class="text-uppercase-white text-light mb-3">{{rutina_jueves.repeticiones}}</h6>
                                <h5 class="text-uppercase text-primary mb-4">{{rutina_jueves.ejercicio}}</h5>
                                <img class="img-fluid rounded" v-bind:src="rutina_jueves.url_img" alt="imagen_ejercicio" />
