                <div id="tab-6" class="tab-pane fade p-0">
                    <div class="row g-5">

                        <div class="col-lg-3 col-md-4 col-sm-6" v-for="rutina_sabado in sabado">
                            <div class="bg-dark rounded text-center py-5 px-3">
                                <h6 class="text-uppercase-white text-light mb-3">{{rutina_sabado.repeticiones}}</h6>
                                <h5 class="text-uppercase text-primary mb-4">{{rutina_sabado.ejercicio}}</h5>
                                <img class="img-fluid rounded" v-bind:src="rutina_sabado.url_img" alt="imagen_ejercicio" />
                            </div>
                        </div>
                        
                    </div>
                </div>

                <div id="tab-7" class="tab-pane fade p-0">
                    <div class="row g-5">

                        <div class="col-lg-3 col-md-4 col-sm-6" v-for="rutina_domingo in domingo">
                            <div class="bg-dark rounded text-center py-5 px-3">
                                <h6 class="text-uppercase-white text-light mb-3">{{rutina_domingo.repeticiones}}</h6>
                                <h5 class="text-uppercase text-primary mb-4">{{rutina_domingo.ejercicio}}</h5>
                                <img class="img-fluid rounded" v-bind:src="rutina_domingo.url_img" alt="imagen_ejercicio" />
                            </div>
                        </div>

                    </div>
