            </div>
        </div>
    </div>
        
    
</template>

<script>
import ejercicios from '@/assets/ejercicios'
export default {
    name: 'Routine',
    data() {
        return {
            lunes: [],
            martes: [],
            miercoles: [],
            jueves: [],
            viernes: [],
            sabado: [],
            domingo: []
        }
    },
    mounted(){

        for (let i = 0; i < ejercicios.length; i++) {
            
            if(ejercicios[i].dia == 'Lunes'){
                this.lunes.push(ejercicios[i])
            }else if(ejercicios[i].dia == 'Martes'){
                this.martes.push(ejercicios[i])
            }else if(ejercicios[i].dia == 'Miercoles'){
                this.miercoles.push(ejercicios[i])
            }else if(ejercicios[i].dia == 'Jueves'){
                this.jueves.push(ejercicios[i])
            }else if(ejercicios[i].dia == 'Viernes'){
                this.viernes.push(ejercicios[i])
            }else if(ejercicios[i].dia == 'Sabado'){
                this.sabado.push(ejercicios[i])
            }else if(ejercicios[i].dia == 'Domingo'){
                this.domingo.push(ejercicios[i])
            }
        }
        
    }
}
</script>